





























































import mTitle from '@/components/m-title'
import mVipCard from '@/components/m-vip-card'
export default {
	components: {
		mTitle,
		mVipCard
	},
	data() {
		return {
			myMember:{},
			members:[],
			userData:{}
		};
	},
	methods:{
		getUser(){
			this.userData = JSON.parse(uni.getStorageSync('userData'));
			console.log(this.userData);
		},
		// 会员列表
		getVips(){
			let _this = this;
			this.mPost("/server/m/members",{}).then(res=>{
				if(res.code==1){
					_this.members=res.data.members
				}
			})
		},
		changeType(memberType){
			switch(memberType){
				case '0':
				return "月";
				case '1':
				return "季";
				case '2':
				return "半年";
				case '3':
				return "年";
			}
		},
		//我的会员
		myVips(){
			let _this = this;
			this.mPost("/server/m/myMember",{}).then(res=>{
				if(res.code==1){
					let data = res.data.myMember;
					if(data['memberType']){
						data['memberType']= _this.changeType(data['memberType']);
					}
					if(data['discount']){
						data['discount']=_this.accMul(data['discount'],10)+'折'
					}
					_this.myMember=data;
				}
			})
		},
		//购买vip
		buyVipFn(){
			let _this = this;
			this.mPost("",{}).then(res=>{
				console.log(res);
			})
		}
	},
	onLoad(options){
		this.getUser();
		this.getVips();
		this.myVips();
	}
}
